











































import {Component, Vue} from 'vue-property-decorator'
import PizzaCart from '@/components/PizzaCart.vue'

@Component({
  components: {
    PizzaCart,
  },
})
export default class Profile extends Vue {

  get orders() {
    return this.$store.getters['auth/orders']
  }

  get pizza() {
    return this.$store.getters['pizza/pizza']
  }

  getItem(item: any) {
    return {
      id: item.pizza_id,
      count: item.count,
    }
  }

  loadPizza() {
    this.$store.dispatch('pizza/get')
  }

  mounted() {
    this.$store.commit('loading/setLoading', true)
    if (!this.pizza.length) this.loadPizza()
    this.$store.dispatch('auth/orders').finally(() => this.$store.commit('loading/setLoading', false))
  }
}

