


















import {Component, Model, Ref, Vue} from 'vue-property-decorator'

@Component
export default class Quantity extends Vue {
  @Ref('quantity') quantityEl!: HTMLFormElement
  @Model('input') readonly count!: number

  increment() {
    this.$emit('input', this.count + 1)
  }

  decrement() {
    if (this.count > 1) {
      this.$emit('input', this.count - 1)
    }
  }
}
